<template>
    <div>
        <el-dialog
  title="注册"
  :visible.sync="isRegister"
  width="30%"
  center>
  <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
  <el-form-item label="账号" prop="userName">
    <el-input type="text" v-model="ruleForm.userName" autocomplete="off"></el-input>
  </el-form-item>
  <el-form-item label="密码" prop="pass">
    <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
  </el-form-item>
  <el-form-item label="确认密码" prop="checkPass" >
    <el-input v-model.number="ruleForm.checkPass" type = "password"></el-input>
  </el-form-item>
   <el-form-item label="手机号" >
    <el-input v-model.number="ruleForm.pohone" type = "text"></el-input>
    <el-button type="primary" @click="getCode">获取验证码</el-button>
  </el-form-item>
   <el-form-item label="验证码"  >
    <el-input v-model.number="ruleForm.code" type = "text"></el-input>
  </el-form-item>
   
 
 
  <el-form-item>
    <el-button type="primary" @click="register1">注册</el-button>
    
  </el-form-item>
</el-form>
    
</el-dialog>
        
    </div>
</template>

<script>


export default {
    props: ["register"],
   
     data() {
      var checkUser = (rule, value, callback) => {

        if (!value) {
          return callback(new Error('用户名不能为空'));
        }
        setTimeout(() => {
          
            if (value.length<5) {
              callback(new Error('5个字符以上'));
            } 
            if(value.lenght>=5){
                // 用户是否存在
                 this.$axios
          .post("/api/users/findUserName", {
            userName: this.RegisterUser.name
          }).then(res => {
            // “001”代表用户名不存在，可以注册
            if (res.data.code == "001") {
              this.$refs.ruleForm.validateField("checkPass");
              return callback();
            } else {
              return callback(new Error(res.data.msg));
            }
          })
            }
            
             else {
              callback()
            }
          
        }, 1000);
      };
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.ruleForm.checkPass !== '') {
            this.$refs.ruleForm.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value != this.ruleForm.pass) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      return {
          isRegister :false,
        ruleForm: {
          pass: '',
          checkPass: '',
         userName: '',
         pohone:'',
         code:''
        },
        rules: {
          pass: [
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ],
          userName: [
            { validator: checkUser, trigger: 'blur' }
          ]
        }
      };
    },
    watch:{
        isRegister:function(val){
            if(!val){
                this.$emit('fromchild', val);
            }

        },
          register: function(val) {
      if (val) {
        this.isRegister = val;
       
      }
    },

    },
    methods: {
      getCode(){
        this.$axios.post('http://http://8.141.56.170:9100/user/sendregcode',{
          mobilephone:this.ruleForm.pohone,
          type:0
        }).then((res)=>{
          console.log(res.data);
        })
      },
      register1(){
        console.log({mobilephone:this.ruleForm.pohone,
          password:this.ruleForm.pass,
          username:this.ruleForm.userName,
          vercode:this.ruleForm.code,})
        
        this.$axios.post('http://http://8.141.56.170:9100/register',{
          mobilephone:this.ruleForm.pohone,
          password:this.ruleForm.pass,
          username:this.ruleForm.userName,
          vercode:this.ruleForm.code,
          
         
        }).then(res=>{
          console.log(res.data)
        })
      }
      
  }
  }
</script>


