<template>
  <div>

  <!-- 轮播图 -->
    <div class="block">
   
    <el-carousel height="460px" id="turn-image">
      <el-carousel-item v-for="item in carousel" :key="item.carousel_id">
       <img style="height:460px;" :src="$target + item.imgPath" :alt="item.describes" />
      </el-carousel-item>
    </el-carousel>
  </div>
  <!-- --------------- -->

<!-- 商品展示 -->


 <div class="one">
          <div class="box-hd">
            <div class="title">分类1
            </div>
          </div>
          <div class="box-bd">
            <div class="promo-list">
              <router-link to>
                <img  class="ex" :src="$target +'public/imgs/phone/phone.png'"  style="margin-top:10px">
              </router-link>
            </div>
            <div class="list">
              <List :list="firstList" :isMore="true"></List>
            </div>
          </div>
        </div>

        <div class="two">
           <div class="box-hd">
             <br>
            <div class="title">分类2</div>
            <div class="more" id="more">
              <Menu :val="2" @fromChild="getChildMsg" >
                <span slot="1" class="cate">子分类1</span>
                <span slot="2" class="cate">子分类2</span>
              </Menu>
            </div>
          </div>
          <div class="box-bd">
            <div class="promo-list">
           
                
                
                  <img class="ex" :src="$target +'public/imgs/appliance/appliance-promo2.png'" style="margin-top:10px">
                
             
            </div>
            <div class="list">
              <List :list="secondList" :isMore="true"></List>
            </div>
          </div>
        </div>

        <!-- ------ -->


  </div>
</template>

<script>
import { mapActions } from 'vuex';

import List from '../components/List'

import Menu from '../components/menu.vue';
export default {
  components:{List,Menu},
 
  data() {
    return {
       carousel: "",
        firstList: "",
       secondList:"",
        twoActive:1,
        twoFlist:'',
        twoSlist:''
      
    };
  },
 
  
  created(){
   
     // 获取轮播图数据
    this.$axios
      .post("/api/resources/carousel", {})
      .then(res => {
        this.carousel = res.data.carousel;
      })
        
      .catch(err => {
        return Promise.reject(err);
      });
        this.getPromo("手机", "firstList");
         this.getPromo(
      ["电视机", "空调", "洗衣机"],
      "twoSlist",
      "/api/product/getHotProduct"
    );
    this.getPromo("电视机", "twoFlist");
     this.getPromo("电视机", "secondList");
     this.setAdmin(false)
   


   
      
   
  },
  mounted() {
    
  },
   watch:{
    twoActive:function(val){
     
      if(val == 1){
        this.secondList = this.twoFlist}
        if(val==2){
          this.secondList = this.twoSlist
        }
    }
   },
  methods: {
    ...mapActions(['setAdmin']),
      getPromo(categoryName, val, api) {
      api = api != undefined ? api : "/api/product/getPromoProduct";
      this.$axios
        .post(api, {
          categoryName
        })
        .then(res => {
          this[val] = res.data.Product;
          
        })
        .catch(err => {
          return Promise.reject(err);
        });
    },
    getChildMsg(val){
      this.twoActive = val
     
     
      
      
      
    
    }
    
  },
};
</script>

