import Vue from 'vue'
import VueRouter from 'vue-router'
import collect from '../views/collect.vue'
import order from '../views/order.vue'
import shopCar from '../views/shopCar.vue'
import Home from '../views/Home.vue'
import shoppingCar from '../views/shoppingCar'
import goods from '../views/goods'
import details from '../views/details.vue'
import adminLogin from '../admin/adminLogin.vue'
import adminHome from '../admin/adminHome.vue'


Vue.use(VueRouter)


const routes = [
  {path:'/order',
component:order},
{path:'/collect',
component:collect},
{path:'/shopCar',
component:shopCar},
{path:'/',
component:Home},
{path:'/shoppingCar',
component:shoppingCar,
},
{path:'/goods',
component:goods},
{path:'/goods/details',
component:details},
{path:'/adminLogin',
component:adminLogin},
{path:'/adminHome',
component:adminHome,
children:[
  {path:'/usersList',
  component:()=>import('../admin/usersList.vue')
},
{path:'/ordersList',
component:()=>import('../admin/ordersList.vue')},
{path:'/cateList',
component:()=>import('../admin/cateList.vue')},
{path:'/goodsList',
component:()=>import('../admin/goodsList.vue'),
},
{path:'/rightsList',
component:()=>import('../admin/rightsList.vue')},
{path:'/rolesList',
component:()=>import('../admin/rolesList.vue')},
{path:'/addGoods',
component:()=>import('../admin/addGoods.vue')}
]

},

 


]

const router = new VueRouter({
  routes
})

export default router
