export default {
    state: {
      user: "", // 登录的用户
      showLogin: false, // 用于控制是否显示登录组件,
      isadmin:localStorage.getItem('isadmin'), //管理员页面
      seller:localStorage.getItem('seller')  //卖家

    },
    getters: {
      getUser (state) {
        return state.user
      },
      getShowLogin (state) {
        return state.showLogin
      },
      getIsAdmin(state){
        return state.isadmin
      },
      getSeller(state){
        return state.seller
      }
    },
    mutations: {
      setUser (state, data) {
        state.user = data;
      },
      setShowLogin (state, data) {
        state.showLogin = data;
      },
      setAdmin(state,data){
        state.isadmin = data
      },
      setSeller(state,data){
        state.seller = data
      }
    },
    actions: {
      setUser ({ commit }, data) {
        commit('setUser', data);
      },
      setShowLogin ({ commit }, data) {
        commit('setShowLogin', data);
      },
      setAdmin({ commit },data){
        commit('setAdmin',data);
      },
      setSeller({ commit },data){
        commit('setSeller',data)
      }
    }
  }