<template>
    <div class="adminHome">
        <el-container style="height:100vh">
    <el-header>
      <div>
        <img src="../assets/logo.png" alt />
        <span>商城后台</span>
      </div>
      <el-button type="info" @click="logout">退出({{this.$store.state.user.seller||'管理员'}})</el-button>
    </el-header>
    <el-container>
      <el-aside :width="isCollapse ? '64px' : '200px'">
        <div class="toggle-button" @click="toggleCollapse">|||</div>
        <el-menu
          router
          background-color="#333744"
          text-color="#fff"
          active-text-color="#409EFF"
          unique-opened
          :collapse="isCollapse"
          :collapse-transition="false"
          :default-active="$route.path"
        >
          <el-submenu :index="item.id+''" v-for="item in menulist" :key="item.id">
            <template slot="title">
              <i :class="iconObj[item.id]"></i>
              <span>{{item.authName}}</span>
            </template>
            <el-menu-item :index="'/'+subItem.path" v-for="subItem in item.children" :key="subItem.id">
              <template slot="title">
                <i class="el-icon-menu"></i>
                <span>{{subItem.authName}}</span>
              </template>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
        
        
    </div>
</template>

<script>
import { mapActions } from 'vuex';
export default {
    
    data() {
    return {
menulist: [{id:125,authName:'用户管理',children:[{authName:'用户列表',path:'usersList'}]},
             {id:103,authName:'权限管理',children:[{authName:'角色列表',path:'rolesList'},{authName:'权限列表',path:'rightsList'}]},
               {id:101,authName:'商品管理',children:[{authName:'商品列表',path:'goodsList'},{authName:'商品分类',path:'cateList'
               }]},
               {id:102,authName:'订单管理',children:[{authName:'订单列表',path:'ordersList'}]} 
                 
                ],
      iconObj: {
        "125": "iconfont icon-icon_user",
        "103": "iconfont icon-tijikongjian",
        "101": "iconfont icon-shangpin",
        "102": "iconfont icon-danju",
        "145": "iconfont icon-baobiao"
      },
      isCollapse: false,
    };
  },
  methods: {
    ...mapActions(['setSeller','setAdmin']),
    logout() {
      window.sessionStorage.clear;
      this.$router.push("/");
      this.setAdmin(false)
      localStorage.setItem('seller','')
      

    },
     
    toggleCollapse() {
      this.isCollapse = !this.isCollapse;
    }
  },
  created() {
    if(this.$store.state.user.seller){
      this.menulist.splice(0,1)
       this.menulist.splice(0,1)
    
    }
   
  }
};
</script>

<style >
 .adminHome .el-header {
  background-color: #373d41;
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  align-items: center;
  color: #fff;
  font-size: 20px;}
  .adminHome .el-header div {
    display: flex;
    align-items: center;}
    .adminHome .el-header div span {
      margin-left: 15px;
    }
  
 .adminHome .el-header img {
    width: 50px;
    height: 50px;
  }


 .adminHome .el-aside {
  background-color: #333744;}
  .adminHome .el-aside.el-menu {
    border-right: none;
  }


.adminHome .el-main {
  background-color: #eaedf1;
}

.adminHome .iconfont {
  margin-right: 10px;
}

.adminHome .toggle-button {
  background-color: #4a5064;
  font-size: 10px;
  line-height: 24px;
  color: #fff;
  text-align: center;
  letter-spacing: 0.2em;
  cursor: pointer;
}
*{
    margin: 0;
    padding: 0;
}

</style>