<template>
    <div class="menu" id="myMenu">
    <ul>
      <li
        v-for="item in val"
        :key="item"
        :class="activeClass == item ? 'active':''"
        @mouseover="mouseover($event,item)"
      >
        <router-link to>
          <slot :name="item"></slot>
        </router-link>  
      </li>
    </ul>
  </div>
</template>

<script>
export default {
    props:['val'],
   
    data() {
        return {
            activeClass :1,
            
        };
    },
    mounted() {
        
    },
    methods: {
        mouseover(e,val){
            this.activeClass = val
            
        }
        
    },
  watch:{
      activeClass:function(val){
          this.$emit('fromChild',val)
         
          
          
      }
  }
};
</script>

<style >
 .menu ul li {
  float: right;
  margin-left: 30px;
  position: relative;
  right: -50px;
  top: -110px;


  

  
}

.menu  a:hover {
  color: #ff6700;
  border-bottom: 2px solid #ff6700;
  text-decoration: none;

}

.menu .active a {
  color: #ff6700;
  border-bottom: 2px solid #ff6700;
   text-decoration: none;

}
.menu a{text-decoration: none;
margin-bottom: 0;

}

</style>