<template>
 <div>
    <div class="adminlogin_container">
      <div class="login_box">
        <!-- 头像 -->
        <div class="avatar_box">
          <img src="../assets/logo.png" alt="">
        </div>
        <!-- 表单 -->
        <el-form ref="LoginFormRef" :model="loginForm" label-width="0" :rules="LoginFormRules" class="login_form">
          <el-form-item prop="username">
            <el-input v-model="loginForm.username" prefix-icon="el-icon-user"></el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input v-model="loginForm.password" prefix-icon="el-icon-lock" type="password"></el-input>
          </el-form-item>
          <el-form-item class="btns">
            <el-button type="primary" @click="login">登录</el-button>
           
          </el-form-item>
        </el-form>  
      </div>
  </div>
 </div>
  
</template>

<script>
import {mapActions} from 'vuex'
export default {
  
    data() {
      return {
        loginForm:{
          username:'',
          password:''
        },
        LoginFormRules:{
          username:[
            { required: true, message: '请输入用户名', trigger: 'blur' },
          ],
          password:[
            { required: true, message: '请输入密码', trigger: 'blur' },
          ]
        }
      }
    },
    methods: {
      ...mapActions(['setSeller']),
     register(){
         //商家注册

     },
      login() {
        this.$refs['LoginFormRef'].validate(async (valid) => {
          if (valid) {
              
              //登录
              if(this.loginForm.username=='admin'&&this.loginForm.password==123456)
              this.$router.push('adminHome')
              if(this.loginForm.username == 'seller1'&& this.loginForm.password ==123456){
              localStorage.setItem('seller',this.loginForm.username)
              this.setSeller(this.loginForm.username)
               console.log(this.$store.state.user.seller)
              this.$router.push('adminHome')
             
              
              
            }
              
            
          } else  {
          
           
            return false
          }
        })
      }
    }
   }
</script>

<style >
.adminlogin_container .login_container{
  background-color: #2b4b6b;
  height: 100%;
}

.adminlogin_container .login_box{
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%)
}

.adminlogin_container .avatar_box{
  width: 130px;
  height: 130px;
  border: 1px solid #eee;
  border-radius: 50%;
  padding: 10px;
  box-shadow: 0 0 10px #ddd;
  position: absolute;
  left:50%;
  transform: translate(-50%,-50%);
  background-color: #fff;}
 .adminlogin_container img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #eee;
  }


.adminlogin_container .login_form{
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
}

.adminlogin_container .btns{
  display: flex;
  justify-content:flex-end;
}

</style>